.normal {
    font-weight: normal;
}

.bold {
    font-weight: bold;
}

.font-ubuntu {
    font-family: Ubuntu;
}
@for $i from 1 to 80 {
    .fs-#{$i} {
        font-size: $i + px;
    }

    .lh-#{$i} {
        line-height: $i + px;
    }
}

@for $i from 4 to 9 {
    .fw-#{$i * 100} {
        font-weight: $i * 100;
    }
}

.fw700 {
    font-weight: 700;
}
.fw400 {
    font-weight: 400;
}
.uppercase {
    text-transform: uppercase;
}
