.sidebar {
    position: fixed;
    width: 300px;
    height: 100%;
    top: 0px;
    right: -300px;
    transition: right 0.5s ease 0s;
    background-color: #FFFFFF;
    z-index: 9;
    font-family: Raleway;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 21px;
    color: #515151;
}
.active-sidebar {
    right: 0px;
}
.sidecloseBtn {
    position: absolute;
    right: 25px;
    top: 25px;
}
.side-list-item {
    display: block;
    width: 100%;
    height: 100%;
    text-decoration: none;
    color: #515151 !important;
    font-size: 16px;
    line-height: 21px;
    padding: 12px 0px 2px 0px;
}
ul {
    list-style-type: none;
}
.dropdownchevron {
    transition: all 0.1s ease 0s;
}
.activedropdownchevron {
    transform: rotate(180deg);
}