.blog-container {
    min-height: calc(100vh - 195px);
    padding-top: 60.8px;
    .blog-mid-container {
        // width: 1060px;
        width: calc(100% - 2 * 190px);
        margin: 0px 190px;
        .breadcrumb {
            font-family: Raleway;
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 16px;
            margin: 20px 0px;
            p {
                margin: 0 10px;
                // display: flex;
                // align-items: center;
                color: #00acd1;
            }
            p:first-of-type {
                margin-left: 0px;
            }
            p:last-of-type {
                margin-right: 0px;
                color: black;
            }
        }
        .handalt {
            width: 250px;
        }
        .blog-content {
            width: calc(100% - 250px);
            .content-heading {
                font-family: Nunito;
                font-size: 24px;
                font-style: normal;
                font-weight: 600;
                line-height: 27px;
                letter-spacing: 0em;
                text-align: left;
            }
            .content-text {
                font-family: Nunito;
                font-size: 20px;
                font-style: normal;
                font-weight: 400;
                line-height: 27px;
                letter-spacing: 0em;
                text-align: left;
            }
            code {
                word-break: break-word;
                display: inline-block;
                white-space: normal;
            }
        }
        .heading-image {
            height: 300px;
            width: 100%;
            border-radius: 10px;
            margin-bottom: 30px;
            object-fit: contain;
            background: #cccccc;
        }
        .publisher-profile-img {
            width: 50px;
            height: 50px;
            margin-right: 10px;
            border-radius: 10px;
        }
        .side-heading {
            font-family: Raleway;
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            line-height: 19px;
            /* identical to box height */
            display: flex;
            align-items: center;
            color: #747474;
        }
        .publisher-name {
            font-family: Raleway;
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            line-height: 19px;
            /* identical to box height */
            display: flex;
            align-items: center;
            text-decoration-line: underline;
            color: #000000;
            margin-top: 12px;
        }
        .side-accessed {
            font-family: Raleway;
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            line-height: 19px;
            display: flex;
            align-items: center;
            color: #000000;
            margin-top: 12px;
        }
        .side-date {
            font-family: Nunito;
            font-style: normal;
            font-weight: 300;
            font-size: 16px;
            line-height: 22px;
            /* identical to box height */

            display: flex;
            align-items: center;
            margin-top: 12px;
            color: #000000;
        }
    }
}
@media screen and (max-width: 1200px) {
    .blog-mid-container {
        width: 100% !important;
        // padding: 20px;
        margin: 0px 50px !important;
    }
    .heading-image {
        height: 200px !important;
    }
}
@media screen and(max-width: 800px) {
    .blogheader {
        font-size: 30px !important;
    }
    .handalt {
        width: 100% !important;
        flex-direction: row !important;
        justify-content: space-around !important;
    }
    .blog-content {
        margin-top: 30px;
        width: 100% !important;
    }
}
@media screen and (max-width: 600px) {
    .blog-mid-container {
        width: 100% !important;
        // padding: 20px;
        margin: 0px 20px !important;
    }
    .heading-image {
        height: 200px !important;
    }
}